@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  @apply bg-primary text-primary
}

.rounded-div {
  @apply border border-secondary rounded-2xl shadow-xl bg-primary px-2 max-w-[1140px] w-full mx-auto
}

.dark {
  --color-bg-primary: #2d3748;
  --color-bg-secondary: #283141;
  --color-text-primary: #dbdbdb;
  --color-text-secondary: #e2e8f0;
  --color-text-accent: #81e6d9;
  --color-bg-input: #4a5361;
  --color-bg-button: #81e6d9;
}

.light {
  --color-bg-primary: #ffffff;
  --color-bg-secondary: #edf2f7;
  --color-text-primary: #2d3748;
  --color-text-secondary: #4a5568;
  --color-text-accent: #2b6cb0;
  --color-bg-input: #edf2f7;
  --color-bg-button: #2b6cb0;
}

.Moon {
  @apply text-primary text-2xl mr-2
}

.Sun {
  @apply text-primary text-2xl mr-2 
}

.SunTheme {
  @apply flex items-center cursor-pointer
}

.MoonTheme {
  @apply flex items-center cursor-pointer
}

.ToggleCSS {
  @apply p-2
}

.navbar-main {
  @apply flex items-center justify-between h-20 font-bold 
}

.navbar-logo {
  @apply text-2xl
}

.toggleNav {
  @apply hidden md:block
}

.buttonNav {
  @apply hidden md:block
}

.signupbutton {
  @apply bg-button text-btnText px-5 py-2 ml-2 rounded-2xl shadow-lg
}

.signinbutton {
  @apply p-4 hover:text-accent
}

.menuIcon {
  @apply block md:hidden cursor-pointer z-10 
}

.mobileMenu {
  @apply md:hidden fixed left-0 top-20 flex flex-col items-center justify-between w-full h-[90%] bg-primary ease-in duration-300 z-10
}

.mobileUL {
  @apply w-full p-4
}

.mobileLI {
  @apply border-b py-6
}

.themeLI {
  @apply py-6
}

.linkButtons {
  @apply flex flex-col w-full p-4
}

.mobileSignIn {
  @apply w-full my-2 p-3 bg-primary text-primary border border-secondary rounded-2xl shadow-xl
}

.mobileSignUp {
  @apply w-full my-2 p-3 bg-button text-btnText rounded-2xl shadow-xl
}

.mobileMenu2 {
  @apply fixed left-[-100%] top-20 h-[90%] flex flex-col items-center justify-between ease-in duration-300 
}

.coinList {
  @apply rounded-div my-4 
}

.coinList2 {
  @apply flex flex-col md:flex-row justify-between pt-4 pb-6 text-center md:text-right
}

.searchCrypto {
  @apply text-2xl font-bold my-2
}

.searchInput {
  @apply w-full bg-primary border border-input px-4 py-2 rounded-2xl shadow-xl
}

.table {
  @apply w-full border-collapse text-center
}

.tableRow {
  @apply border-b
}

.rank {
  @apply px-4
}

.coin {
  @apply text-left
}

.hideTable {
  @apply hidden md:table-cell
}

.hideTable2 {
  @apply hidden sm:table-cell
}